@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@300&family=Open+Sans&display=swap);
* {
	margin: 0;
}

body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

/* html {
	font-size: 62.5%;
} */

/* Below are the general styles - linking different fonts to their elements. */

body,
ul,
h1,
h2,
h3 p {
	margin: 0;
	padding: 0;
}

h1,
h2,
h3 {
	font-family: "Inconsolata", monospace;
}

body {
	font-family: "Open Sans", sans-serif;
	background-color: #eef0f1;
}

.wrapper {
	max-width: 1100px;
	width: 100%;
	margin: 0 auto;
}

/* Below are styles for the nav*/

.topnavcontainer {
	margin-bottom: -0.3rem;
}

.topnav img {
	height: 2.5rem;
	padding: 1rem;
	border-radius: 10px;
}

.topnav {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.topnav ul {
	list-style: none;
	display: flex;
	padding: 1rem;
}

.topnav .desk a {
	border-bottom: 1px solid black;
	padding: 0.3rem;
}

.topnav .desk a {
	text-decoration: none;
	font-size: 1.1rem;
	color: black;
	margin: 1rem;
}

.topnav ul a:hover {
	background-color: black;
	color: white;
}

/* Below are the Styles for the header*/

.head {
	min-width: 100%;
	/* 	background-color: rgba(60, 77, 115, 1); */
	background-color: #040d21;
	background-size: cover;
}

.head .headertext {
	color: #9ef5d1;
	letter-spacing: 0.1rem;
	text-align: center;
	padding: 7rem;
}

.headertext h1 {
	font-size: 2.5rem;
	margin-bottom: 2rem;
}

.headertext h2 {
	font-size: 1.5rem;
	color: white;
	word-break: break-word;
}

/* media queries */

/* media */

@media (max-width: 1024px) {
	.topnav {
		justify-content: space-between;
	}
}

@media (max-width: 834px) {
	.topnav ul a:hover {
		background-color: transparent;
	}

	.topnav img {
		margin-left: 1rem;
	}

	.topnav {
		display: flex;
		justify-content: space-between;
	}
}

@media (max-width: 600px) {
	.head .headertext {
		padding: 5rem;
	}

	.head h1 {
		font-size: 2rem;
	}

	.head h2 {
		font-size: 1.35rem;
	}
}

@media (max-width: 500px) {
	.head .headertext {
		padding: 3rem;
	}
}

.blog_topics {
	padding: 3rem;
	font-weight: 100;
	letter-spacing: 0.1rem;
}

.blog_topics .flex-container {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

.blog_topics .container {
	box-shadow: 0px 0px 10px lightgrey;
	width: calc((100% / 3) - 3%);
	margin: 1%;
	background-color: #8193b2;
	border-radius: 10px;
	transition: transform 1s;
	cursor: pointer;
}

.blog_topics .container img {
	padding-top: 3rem;
	width: 15rem;
	height: 10rem;
	display: block;
	margin: auto;
	-webkit-filter: hue-rotate(50deg);
	        filter: hue-rotate(50deg);
}

.blog_topics .container h2 {
	text-align: center;
	font-size: 1.5rem;
	font-weight: 100;
	margin-top: 2rem;
	padding: 0.5rem;
	background-color: #040d21;
	color: white;
}

.blog_topics .postnumbers {
	font-size: 1.1rem;
	color: lightgreen;
}
.blog_topics .container p {
	font-size: 1.2rem;
	line-height: 30px;
	text-align: center;
	padding: 0.5rem;
}

/* media */

@media (max-width: 1024px) {
	.blog_topics .container {
		width: calc((100% / 2) - 2%);
	}
}

@media (max-width: 600px) {
	.blog_topics {
		margin: 0.5rem;
		padding: 0rem;
		margin-top: 2rem;
	}
	.blog_topics .flex-container {
		display: block;
		margin: auto;
		margin-right: 5rem;
		margin-left: 5rem;
	}
	.blog_topics .container {
		width: calc((100% / 1) - 2%);
		margin-bottom: 2rem;
	}

	.blog_topics .container p {
		padding-bottom: 3rem;
		font-size: 1.1rem;
	}
}

@media (max-width: 500px) {
	.blog_topics .flex-container {
		margin-right: 2rem;
		margin-left: 2rem;
	}
}

@media (max-width: 384px) {
	.blog_topics .flex-container {
		margin-right: 1rem;
		margin-left: 1rem;
	}

	.blog_topics .container p {
		padding-left: 1rem;
		padding-right: 1rem;
	}
}

